@import url(https://fonts.googleapis.com/css2?family=Orbitron&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Euphoria+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: "Orbitron", sans-serif;
}

* {
  color: #000000;
  margin: 0;
  padding: 0;
}
body {
  background: #000000;
}

a {
  text-decoration: none;
  color: inherit;
  outline: none;
  transition: 0.5s ease;
  -o-transition: 0.5s ease;
  -webkit-transition: 0.5s ease;
}

#topSection {
  background-color: #a21514;
  width: 100%;
}

#navigation {
  background-color: #a21514;
  width: 100%;
}

#bodylayout {
  background-image: linear-gradient(#000000, #a21515);
  width: 100%;
}

.linespace {
  background-color: #333;
  width: 100%;
  height: 6px;
}

.container {
  display: block;
  position: relative;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  width: 1010px;
}

.containerContent {
  width: 100%;
}

.containerLayout {
  display: -webkit-flex;
  display: flex;
  position: relative;
  background: url(data:image/png;base64,) 0 0 repeat-x #fff;
  width: 100%;
  box-shadow: 0 5px 5px #000;
  padding-bottom: 30px;
}

.content {
  float: left;
  width: 75%;
}

.ads-horizontal,
.ads-vertical,
.ads-vertical-home,
.ads-featured-section {
  z-index: 3;
}
.ads-horizontal {
  float: right;
  z-index: 999;
  margin-top: 20px;
  margin-right: 2%;
  width: 23%;
  height: 500px;
}

.ads-vertical {
  position: relative;
  width: 100%;
}

.ads-vertical-home {
  border-top: 1px solid #dadada;
  border-bottom: 1px solid #dadada;
  border-right: 1px solid #dadada;

  float: left;
  width: 700px;
  height: 200px;
  margin-left: 4%;
  margin-top: 20px;
}

.ads-featured-section {
  display: block;
  content: "";
  height: 320px;
}
.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
}

h1 {
  color: black;
}

/* Tab Css*/
.tabContainer {
  display: block;
  padding-top: 20px;
}

.card {
  display: inline-block;
  position: relative;
  width: 33%;
}

.cardImg {
  -o-box-sizing: border-box;
  box-sizing: border-box;
  object-fit: cover;
  width: 232px;
  height: 165px;
  margin-top: 4px;
  margin-bottom: 16px;
}

.cardText h4,
p {
  font-family: Tahoma, Verdana, Segoe, sans-serif;
  color: black;
  text-align: left;
  margin-left: 10px;
}
.cardText h4 {
  margin-top: -7px;
  font-size: 15px;
}

.cardText p {
  margin-top: 5px;
  font-size: 12px;
}

.cardBtn {
  float: left;
  position: relative;
  text-transform: uppercase;
  color: #fff;
  background: url(data:image/png;base64,) 0 0 repeat-x #930706;
  border-bottom: 1px solid #c40a0a;
  border-radius: 5px 5px 0 0;
  margin-bottom: 5px;
  margin-top: 10px;
  margin-left: 10px;
  font: bold 12px/21px "Orbitron", sans-serif;
  padding: 4px 12px;
}

.cardBtn:after {
  display: block;
  content: "";
  position: absolute;
  background: #373636;
  left: 0;
  right: 0;
  height: 5px;
  bottom: -6px;
  border-radius: 0 0 5px 5px;
}

.pagenavi {
  margin-top: 20px;
}
.pagenaviBox {
  display: inline;
  border-style: solid;
  color: white;
  background-color: #292929;
  border-width: 1px;
  margin-right: 10px;
  padding: 5px 10px 5px 10px;
}

.pagenaviBox:hover {
  background-color: #637e91;
}

.pageCurrent {
  background-color: #a21514;
  pointer-events: none;
}

.pageTotal {
  pointer-events: none;
}

@media only screen and (max-width: 1000px) {
  .container {
    width: 100%;
  }
  #topSection {
    width: 100%;
  }

  .linespace {
    display: none;
  }

  .ads-vertical {
    display: -webkit-flex;
    display: flex;
    position: relative;
    width: 100%;
  }

  .ads-horizontal {
    display: block;
    position: relative;
    width: 96%;
  }

  .ads-vertical-home {
    width: 96%;
    height: 200px;
    margin-left: 2%;
    margin-top: 20px;
  }
  .ads-featured-section {
    content: "";
    max-height: 320px;
    min-height: 200px;
  }
}

.topSection {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: left;
          align-items: left;
}
.topSection .logo {
  position: absolute;
  margin-top: 46px;
  z-index: 2;
}

.topSection img {
  float: left;
}
.topSection .search {
  position: relative;
  background-color: #393433;
  width: 100%;
  height: 72px;
  margin-top: 70px;
}

.topSection input {
  float: right;
  box-sizing: border-box;
  background-color: #6c6d6b;
  color: rgb(255, 255, 255);
  margin-top: 22px;
  padding: 7px 5px;
  border: 1px solid #6c6d6b;
  border-radius: 4px 0px 0px 4px;
  width: 20%;
}

.topSection button {
  float: right;
  margin-top: 21px;
  margin-right: 5%;
  border-radius: 0px 5px 5px 0px;
  width: 120px;
  cursor: pointer;
  font: bold 12px/21px;
  padding: 7px 0px;
  text-transform: uppercase;
  color: #fff;
  background: url(data:image/png;base64,) 0 0 repeat-x #930706;
  border-bottom: 1px solid #c40a0a;
  letter-spacing: 3px;
}

@media only screen and (max-width: 1000px) {
  .topSection .logo {
    margin-top: 10px;
  }

  .topSection .search {
    height: 50px;
    margin-top: 60px;
    padding-top: 60px;
  }

  .topSection input {
    margin-top: 0px;
    width: 60%;
  }

  .topSection button {
    margin-top: 0px;
  }
}

.navigation {
  background-color: #1e1e1e;
  width: 100%;
}
.navigation ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-image: linear-gradient(#494848, #141414);
  /*background-color: rgb(39, 38, 38);*/
  border-top: 1px solid rgb(102, 100, 100);
  border-bottom: 1px solid black;
}

.navigation li {
  float: left;
  border-right: 1px solid black;
  text-transform: uppercase;
}

.navigation li a {
  display: block;
  color: white;
  text-align: center;
  padding: 18px 22px;
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 3px;
}

.navigation li a:hover:not(.active) {
  background-color: #111;
}

.navigation .active {
  background: url(data:image/png;base64,) center bottom no-repeat;
  border-bottom: 5px solid #a20707;
  background-color: #222123;
  padding: 16px 22px;
}
@media only screen and (max-width: 1000px) {
  .navigation {
    display: none;
  }
}

.footer {
  display: block;
  background: #1b1b1b;
}

.footer .foot_top {
  background: #292929;
  padding-bottom: 28px;
}

.footer .container_24 {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 96%;
  margin-left: 2%;
}

.footer .grid_14 {
  display: inline-block;
  position: relative;
  width: 50%;
}

.footer .grid_6 {
  display: inline-block;
  position: relative;
  width: 30%;
}
.footer .grid_4 {
  display: inline-block;
  position: relative;
  width: 20%;
}

.footer h3 {
  color: #cf0606;
  text-transform: uppercase;
  font-weight: bold;
  text-align: left;
  font-weight: bold;
  padding-top: 29px !important;
  letter-spacing: 3px;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 8px;
  margin-left: 8px;
}

.footer ul {
  list-style: none;
  text-align: left;
  padding: 0;
  margin: 0;
  margin-left: 20px;
}

.footer li {
  color: #fff;
  font-family: Tahoma, Verdana, Segoe, sans-serif;
  font: bold 12px/22px Tahoma, sans-serif;
  letter-spacing: 3px;
}

.footer li:hover {
  color: #cf0606;
}

.footer li:before {
  content: "";
  display: block;
  position: relative;
  border-color: transparent #6d6d6d;
  border-style: solid;
  border-width: 0.35em 0 0.35em 0.45em;
  height: 0;
  width: 0;
  left: -1em;
  top: 15px;
}

.footer .current a {
  color: #cf0606;
  text-transform: uppercase;
  font: bold 12px/22px "Orbitron", sans-serif;
  letter-spacing: 3px;
}

.footer .contact {
  margin-top: 17px;
}

.footer p {
  font-family: "Arial", Times, serif;
  color: white;
  margin-top: 10px;
  font-size: 15px;
}
.footer .copy {
  color: rgb(66, 212, 248);
}
@media only screen and (max-width: 1000px) {
  .footer .mobile {
    display: none;
  }

  .footer .grip_6 {
    width: 100%;
  }
}

/* Slideshow */

.slideshow {
  overflow: hidden;
  width: 100%;
}

.slideshowSlider {
  margin: 0 auto;
  white-space: nowrap;
  transition: ease 1000ms;
}

.slide {
  display: inline-block;
  position: absolute;
  box-sizing: content-box;
  position: relative;
  width: 100%;
  /* width:1010px; */
  height: 100%;
  border-radius: 40px;
}

/* Buttons */

.slideshowDotsPosition {
  float: right;
  position: absolute;
  right: 15px;
  bottom: 20px;
}

.slideshowDot {
  display: inline-block;
  position: relative;
  cursor: pointer;
  margin: 10px 5px 0px;
  width: 12px;
  height: 12px;
  border: 2px solid white;
  border-radius: 50%;
}

.slideshowDots .active {
  border: 2px solid #a21514;
  background: url(data:image/png;base64,) center bottom no-repeat;
  background-color: #222123;
}

.slideshow img {
  vertical-align: middle;
  width: 100%;
  height: 100%;
}

.slideshow h3,
.slideshow p {
  color: white;
  text-align: center;
  font-family: "Times New Roman", Times, serif;
}

.slideshow img {
  max-height: 800px;
  min-height: 300px;
  object-fit: cover;
  object-position: top;
}
@media only screen and (max-width: 1000px) {
  .slideshow,
  .slideshowDotsPosition {
    display: none;
  }
}

